@import '_variables';

body { padding-top: 165px;

    //*** Breakpoints ***
    @include breakpoint($bp1) { padding-top: 70px; }
} 

div, p, li { font: 14px/18px HelveticaNeueLTStd-Roman, Helvetica, Arial, Sans-Serif; color: #595959; }

a { transition: 0.8s; }


@import '_header';

#topSliderC { position: relative;
    
    #topSliderNavC { position: relative; margin: 0px auto; width: 1260px;
        
        #topSliderArrowsC { position: absolute; top: 278px; left: 0px; width: 100%; z-index: 10; 

            #lftArrow { position: absolute; left: 10px; }
            #rtArrow { position: absolute; right: 10px; }

            //*** Breakpoints ***
            @include breakpoint($bp1) { top: 160px; }

        }//topSlideArrowsC

        #slideNavC { position: absolute; top: 473px; right: 0px; z-index: 10;

            a { display: inline-block; margin-right: 10px; width: 14px; height: 14px; background: #d1d3d4;
            
                &:hover, &.active { background: #4c9d2f; }
                
                
            }//a
            
            
            
            //*** Breakpoints ***
            @include breakpoint($bp1) { top: 320px; }

        }//slideNavC
    
        //*** Breakpoints ***
        @include breakpoint($bp1) { width: 100%; }
        
    }//topSliderNavC
    
    
    .slide { position: relative; height: 508px; max-width: 1260px; background-size: cover; background-position: top center; background-repeat: no-repeat;
        
        .slideTxtBoxC { position: relative; width: 1260px; height: 508px; margin: 0px auto;
            
            .slideTxtBox { position: absolute; bottom: 100px; width: 460px; padding: 15px; background: rgba(255,255,255,0.8);
            
                .slideHdr { padding-bottom: 13px; color: #0c419a; font: 35px/33px HelveticaNeueLT-Light;
                    
                    //*** Breakpoints ***
                    @include breakpoint($bp1) { padding-bottom: 5px; color: #FFF; }
                    @include breakpoint($bp2) { font-size: 20px; line-height: 23px; }
                    
                }//slideHdr

                .slideTxt { height: 63px; margin-bottom: 10px; font: 18px HelveticaNeueLTStd-It; overflow: hidden;
                    
                    //*** Breakpoints ***
                    @include breakpoint($bp1) { height: auto; color: #FFF; }
                    @include breakpoint($bp2) { font-size: 13px; }
                
                }//slideTxt

                a { display: inline-block; padding: 18px 40px; text-align: center; background: #48a540; color: #FFF; text-decoration: none;
                    
                    &:hover { background: #0c419a; }
                    
                    img { margin-left: 24px; vertical-align: middle; 
                    
                        //*** Breakpoints ***
                        @include breakpoint($bp1) { height: 12px; }
                        @include breakpoint($bp1) { height: 10px; }
                        
                    }//img
                    
                    //*** Breakpoints ***
                    @include breakpoint($bp1) { padding: 5px 10px; }
                    @include breakpoint($bp1) { font-size: 11px; }

                }//a
                
                //*** Breakpoints ***
                @include breakpoint($bp1) { bottom: 0px; left: 0px; right: 0px; width: 100%; background: #0c419a; }
                
            }//slideTxtBox
            
            //*** Breakpoints ***
            @include breakpoint($bp1) { width: 100%; margin: 0px;  }
            
        }//slideTxtBoxC
        
        //*** Breakpoints ***
        @include breakpoint($bp3) { background-size: 200%; }
 
    }//slide
    
    .rtLayout .slideTxtBox { right: 60px; }
    .lftLayout .slideTxtBox { left: 60px; }
    
        
}//topSliderC



